import React, { useState, useEffect } from 'react';
import PhoneInput from 'react-phone-input-2';
import 'react-phone-input-2/lib/style.css';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { motion, AnimatePresence } from 'framer-motion';

function PopUpContainer({ onCloseButton, source, onClose }) {
  const [customTime, setCustomTime] = useState('');
  const [name, setName] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');
  const [selectedPackage, setSelectedPackage] = useState('');
  const [timeSelection, setTimeSelection] = useState(' ');
  const [isImmediateTimeSelected, setIsImmediateTimeSelected] = useState(true);

  const showErrorNotification = () => {
    toast.error('!    .', {
      position: 'top-right',
      autoClose: 5000,
    });
  };

  function getMinDateTime() {
    const now = new Date();
    const year = now.getFullYear();
    const month = String(now.getMonth() + 1).padStart(2, '0'); //       '0'  
    const day = String(now.getDate()).padStart(2, '0'); //       '0'  
    const hours = String(now.getHours()).padStart(2, '0'); //       '0'  
    const minutes = String(now.getMinutes()).padStart(2, '0'); //       '0'  

    //      ,    min  input  "datetime-local".
    return `${year}-${month}-${day}T${hours}:${minutes}`;
  }

  function formatDate(dateTime) {
    if (!dateTime) {
      return ''; //   ,  dateTime  .
    }

    const date = new Date(dateTime);
    const day = date.getDate();
    const month = date.getMonth() + 1; //  1,    Date   0.
    const year = date.getFullYear();
    const hours = date.getHours();
    const minutes = date.getMinutes();
    const formattedMinutes = String(minutes).padStart(2, '0');
    //       "::; :".
    return `${day}.${month}.${year}; ${hours}:${formattedMinutes}`;
  }

  const showSuccessNotification = () => {
    toast.success('  ', {
      position: 'top-right',
      autoClose: 5000,
    });
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    if (name && phoneNumber && selectedPackage && (timeSelection !== '' || customTime)) {
      const formData = `
         i  !\n\n -: ${source}\n I': ${name}\n  : +${phoneNumber}\n  : ${selectedPackage}\n  : ${
        timeSelection === ' ' ? timeSelection : formatDate(customTime)
      }
      `.trim();

      try {
        const botToken = '6809113635:AAEAPNVeXhN78oUhxyGEpuahfr1pMTWSLM0';
        const groupId = '-1002050844018';

        const message = encodeURIComponent(formData);
        const url = `https://api.telegram.org/bot${botToken}/sendMessage?chat_id=${groupId}&text=${message}`;

        const response = await fetch(url, {
          method: 'POST',
        });

        if (response.ok) {
          console.log('     Telegram.');
          showSuccessNotification();
          resetForm();
        } else {
          showErrorNotification();
          console.log('    .', response.status, response.statusText);
        }
      } catch (error) {
        console.error(error);
        alert(' .');
        showErrorNotification();
      }
      console.log(formData);
    }
  };
  const resetForm = () => {
    setName('');
    setPhoneNumber('');
    setSelectedPackage('');
    setCustomTime('');
  };

  const popupVariants = {
    hidden: { opacity: 0, y: '100%' },
    visible: { opacity: 1, y: 0 },
  };
  return (
    <AnimatePresence>
      <motion.div
        className={`popUp-wrapper `}
        variants={popupVariants}
        initial="hidden"
        animate="visible"
        exit="hidden">
        {onCloseButton && (
          <svg
            onClick={onCloseButton}
            className="popUp-wrapper--closeButton"
            width="2.5rem"
            height="2.5rem"
            viewBox="0 0 24 24"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
            stroke="#BF1616">
            <g id="SVGRepo_bgCarrier" strokeWidth="0"></g>
            <g
              id="SVGRepo_tracerCarrier"
              strokeLinecap="round"
              strokeLinejoin="round"
              stroke="#Bf1616"
              strokeWidth="0.288"></g>
            <g id="SVGRepo_iconCarrier">
              <path
                d="M14.5 9.50002L9.5 14.5M9.49998 9.5L14.5 14.5"
                stroke="#BF1616"
                strokeWidth="1.5"
                strokeLinecap="round"></path>
            </g>{' '}
          </svg>
        )}
        <form onSubmit={handleSubmit} style={{ display: 'contents' }}>
          <label className="popUp-wrapper__label">
             '
            <input
              type="text"
              name=" '"
              placeholder="'"
              value={name}
              onChange={(e) => setName(e.target.value)}
              autoComplete="off"
              required
            />
          </label>
          <label className="popUp-wrapper__label">
             
            <PhoneInput
              name="number"
              value={phoneNumber}
              onChange={setPhoneNumber}
              country={'ua'}
              enableSearch
              inputStyle={{
                width: '100%',
                border: '1px solid black',
                borderRadius: '11px',
              }}
              placeholder="  "
              autoComplete="off"
              required
            />
          </label>
          <label className="popUp-wrapper__label">
              ?
            <select
              value={timeSelection}
              onChange={(e) => {
                setTimeSelection(e.target.value);
                setIsImmediateTimeSelected(e.target.value === ' ');
                if (e.target.value === '') {
                  setCustomTime('');
                }
              }}
              name="  ?"
              required>
              <option value=" "> </option>
              <option value=""></option>
            </select>
            {timeSelection === '' && (
              <input
                type="datetime-local"
                value={customTime}
                onChange={(e) => setCustomTime(e.target.value)}
                style={{ marginTop: '10px', borderRadius: '11px', width: '100%' }}
                autoComplete="off"
                min={getMinDateTime()}
                required
              />
            )}
          </label>
          <p className="popUp-wrapper__text">    ?</p>
          <label className="popUp-wrapper__radio-label">
            <input
              type="radio"
              name=" "
              value=" "
              onChange={(e) => setSelectedPackage(e.target.value)}
              style={{}}
              required
            />
             
          </label>
          <label className="popUp-wrapper__radio-label">
            <input
              type="radio"
              name=" "
              value="-"
              onChange={(e) => setSelectedPackage(e.target.value)}
              style={{}}
              required
            />
            -
          </label>
          <button className="popUp-wrapper__button" type="submit">
            
          </button>
        </form>
      </motion.div>
    </AnimatePresence>
  );
}

export default PopUpContainer;
